* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  min-height: 100%;
  background: #eee;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  color: #222;
  font-size: 14px;
  line-height: 26px;
  padding-bottom: 50px;
}

.container {
  padding: 0 30px;
  background: #fff;
  margin: 0px auto 0px;
  box-shadow: 1px 1px 2px #dad7d7;
  border-radius: 3px;
}

.resume-container {
  max-width: 1000px;
  height: 1300px;
  display: flex;
  /* justify-content: center;  */
  align-items: flex-start;
}
.div-left-4,
.div-right-4 {
  padding: 30px;
  width: 100%;
}

.header {
  margin-bottom: 30px;
  width: 100%;
}

.header4 {
  padding: 30px 0;
}

.div-col {
  display: flex;
  flex-direction: column;
}
.div-col .margin-3 {
  margin: 3px !important;
}
.header .full-name {
  font-size: 40px;
  text-transform: uppercase;
  margin-bottom: 5px;
}
.header .first-name {
  font-weight: 700;
}
.div-flex-4 {
  justify-content: space-between;
}
.header .last-name {
  font-weight: 300;
}
.header .contact-info {
  margin-bottom: 5px;
}
.coontact-info-4 {
  padding: 25px 0;
}
.header .email,
.header .phone,
.header .linkedIn {
  color: #282a35;
  font-weight: 300;
}
.header .separator {
  height: 10px;
  display: inline-block;
  border-left: 2px solid #999;
  margin: 0px 10px;
}
.header .position {
  font-weight: bold;
  display: inline-block;
  margin-right: 10px;
  text-decoration: underline;
}
.right-details-4 {
  padding: 30px 0;
}
.details {
  line-height: 20px;
}
.section {
  margin-bottom: 25px;
}
.details .section {
  margin-bottom: 40px;
}
.details .section:last-of-type {
  margin-bottom: 0px;
}
.details .section__title {
  letter-spacing: 2px;
  font-weight: bold;
  margin-bottom: 10px;
  text-transform: uppercase;
}
.details .section__list-item {
  margin-bottom: 40px;
}
.details .section__list-item:last-of-type {
  margin-bottom: 0;
}
.details .left,
.details .right {
  vertical-align: top;

  display: inline-block;
}
.details .left {
  width: 60%;
}
.details .right {
  width: 39%;
}
.details .name {
  font-weight: bold;
}
.details a {
  text-decoration: none;
  color: #000;
  font-style: italic;
}
.details a:hover {
  text-decoration: underline;
  color: #000;
}
.details .skills__item {
  margin-bottom: 10px;
}
.details .skills__item .right input {
  display: none;
}
.details .skills__item .right label {
  display: inline-block;
  width: 20px;
  height: 20px;
  background: #c3def3;
  border-radius: 20px;
  margin-right: 3px;
}
.details .skills__item .right input:checked + label {
  background: #79a9ce;
}

.email-val,
.phone-val,
.linkedIn-val,
.website-val {
  color: #222;
}

.language-name {
  padding: 5px 5px 5px 0;
  margin: 10px 10px 10px 0;
}

.div-right-4 {
  min-width: 40%;
}
